.top-content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;

  // .top-content__layout

  &__layout {
    position: relative;
    top: 0;
    width: 100%;
  }

  // .top-content__shadow

  &__shadow {
    img {
      width: 100%;
    }
  }

  // .top-content__bg

  &__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    img {
      width: 100%;
    }
  }

  // .top-content__items

  &__items {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }

  &__item {
    position: absolute;
    &-1 {
      top: 0 !important;
      left: -20px !important;
      padding-right: 91%;
    }
    &-2 {
      right: -20px !important;
      top: -20px !important;
      padding-left: 83%;
      text-align: right;
    }
    &-3 {
      right: -20px !important;
      top: -30px !important;
      padding-left: 18%;
      img {
        width: 105%;
      }
    }
  }

  // .top-content__scene
  &__scene {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
  // .top-content__stars
  &__stars {
    width: 100%;
    height: 100%;
    .star {
      position: absolute;
      &-1 {
        left: 3.8% !important;
        top: 18% !important;
      }
      &-2 {
        left: 13% !important;
        top: 23.6% !important;
      }
      &-3 {
        left: 7.8% !important;
        top: 72.6% !important;
      }
      &-4 {
        left: 9.8% !important;
        bottom: 43% !important;
      }
      &-5 {
        right: 2.8% !important;
        left: auto !important;
        top: 12.8% !important;
      }
      &-6 {
        left: auto !important;
        right: 2.3% !important;
        top: 31% !important;
      }
      &-7 {
        left: 37.5% !important;
        top: 22% !important;
      }
      &-8 {
        left: auto !important;
        right: 30.5% !important;
        top: 14.8% !important;
      }
      &-9 {
        left: 16.2% !important;
        top: 8.5% !important;
        width: 22px;
        height: 22px;
      }
      &-10 {
        left: 26.34% !important;
        top: 33.6% !important;
        width: 22px;
        height: 22px;
      }
      &-11 {
        left: 36.2% !important;
        top: auto !important;
        bottom: 10.2% !important;
        width: 22px;
        height: 22px;
      }
      &-12 {
        left: 45% !important;
        top: 38.6% !important;
        width: 22px;
        height: 22px;
      }
      &-13 {
        left: 59.95% !important;
        top: 21.7% !important;
        width: 22px;
        height: 22px;
      }
      &-14 {
        left: auto !important;
        right: 10.75% !important;
        top: 55.4% !important;
        width: 22px;
        height: 22px;
      }
      &-15 {
        left: auto !important;
        right: 8.4% !important;
        top: auto !important;
        bottom: 29% !important;
        width: 22px;
        height: 22px;
      }
      &-16 {
        left: auto !important;
        right: 21.2% !important;
        top: auto !important;
        bottom: 17.9% !important;
        width: 22px;
        height: 22px;
      }
    }
  }

  // .top-content__row

  &__row {
    position: absolute;
    display: flex;
    justify-content: center;
    margin: 10px 0 0 0;
  }

  // .top-content__col

  &__col {
    transition: margin 0.3s ease;
    &:first-child {
      margin: 0 20px;
    }
    &:last-child {
      margin: 0 20px 0 100px;
    }
    h1 {
      font-family: $ffs;
      margin: 0.9em 0 0 0;
      color: #fff;
      @include adaptiv-font(72, 24);
      max-width: 800px;
      text-shadow: 1px 1px 4px rgba(150, 150, 150, 1);
    }
    p {
      font-family: $ffw;
      color: $wht;
      @include adaptiv-font(23, 14);
      line-height: 2;
      max-width: 600px;
      margin: 0.5em 0 0 0;
      text-shadow: 1px 1px 4px rgba(150, 150, 150, 1);
    }
  }

  // .top-content__btn

  &__btn {
    position: relative;
    padding: 24px 75px 24px 40px;
    &::before {
      position: absolute;
      right: 50px;
      top: 50%;
      transform: translateY(-50%);
      content: "►";
    }
  }
}
.vendors-main {
  .vendors__wrap {
    margin: 70px 0 170px 0;
  }
}
.description {
  margin: 20px 0;
  // .description__small
  padding: 0 15px;
  text-align: center;
  &__small {
    color: #899ea7;
    p {
      margin: 0;
      text-transform: uppercase;
    }
  }

  // .description__big

  &__big {
    margin: 10px 0;
    @include adaptiv-font(60, 20);
    h2 {
      font-size: inherit;
      margin: 0;
    }
  }
}
.steps {
  // .steps__row
  margin: 120px 0 200px;
  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .steps__col

  &__col {
    max-width: 350px;
    text-align: center;
    margin: 40px auto 0;
    padding: 0 15px;
  }

  // .steps__descr

  &__descr {
    font-size: 18px;
    font-weight: 700;
  }

  // .steps__text

  &__text {
    font-size: 15px;
  }
}
.our-services {
  display: flex;
  align-items: center;
  justify-content: center;
  // .our-services__body
  &__body {
    max-width: 90%;
    text-align: right;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  // .our-services__descr
  &__descr {
    font-size: 14px;
    color: #899ea7;
    text-transform: uppercase;
  }
  // .our-services__title
  &__title {
    margin: 0 0 20px 0;
    @include adaptiv-font(60, 20);
  }
  // .our-services__text
  &__text {
    font-size: 15px;
    line-height: 24px;
    max-width: 70%;
  }
  // .our-services__image
  &__image {
    flex: 0 0 60%;
    margin: 0 -9% 0 0;
    img {
      max-width: 100%;
    }
  }
}
.our-services-left {
  .our-services__body {
    margin: 0 auto;
    text-align: left;
    max-width: 76%;
    align-items: flex-start;
  }
  .our-services__text {
    max-width: 56%;
  }
  .our-services__image {
    flex: 0 0 64%;
    margin: 0 0 0 -24%;
  }
}
.portfolio {
  margin: 80px 0 0 0;
  // .portfolio__subtitle

  &__subtitle {
    font-size: 14px;
    color: #899ea7;
    text-transform: uppercase;
  }

  // .portfolio__content

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .portfolio__title

  &__title {
    margin: 0;
    @include adaptiv-font(60, 20);
    h3 {
      margin: inherit;
    }
  }

  // .portfolio__list

  &__list {
    display: flex;
    align-items: center;
    li {
      cursor: pointer;
      position: relative;
      margin: 0 20px 0 0;
      transition: color 0.3s ease, background 0.3s ease, height 0.3s ease;
      font-weight: 700;
      color: #000;
      &:last-child {
        margin: 0;
      }
      &._active {
        color: #8e34ef;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 100%;
          height: 3px;
          opacity: 1;
          background: #8e34ef;
          transition: color 0.3s ease, opacity 0.3s ease;
        }
      }
      a {
        color: inherit;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 3px;
        opacity: 0;
        background: #8e34ef;
        transition: color 0.3s ease, opacity 0.3s ease;
      }

      &:hover {
        color: #8e34ef;
        &::before {
          opacity: 1;
        }
      }
    }
  }

  // .portfolio__slider

  &__slider {
    margin: 20px 0 120px 0;
    padding: 0 0 120px 0;
  }
  // .portfolio_wrapper

  &__wrapper {
    display: flex;
  }
  // .portfolio__box

  &__box {
    position: relative;
    width: 380px;
    max-width: 100%;
    &:hover {
      .portfolio__img {
        img {
          transform: scale(1.1);
        }
      }
      .portfolio__descr {
        opacity: 1;
        bottom: -89px;
        pointer-events: all;
      }
    }
  }

  // .portfolio__img

  &__img {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 400px;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
      transition: transform 0.6s ease;
      margin: 0;
      object-fit: cover;
    }
  }

  // .portfolio__descr

  &__descr {
    position: absolute;
    left: 0;
    bottom: 0;
    opacity: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    transition: opacity 0.6s ease, bottom 0.6s ease;
    &._active {
      z-index: 1;
    }
  }
  // .portfolio__text

  &__text {
    padding: 22px;
  }

  // .portfolio__text-top

  &__text-top {
    font-size: 17px;
    font-weight: 700;
    margin: 0 0 10px 0;
  }

  // .portfolio__text-bot

  &__text-bot {
    font-size: 15px;
    color: #3a4366;
  }

  // .portfolio__show

  &__show {
    position: relative;
    display: inline-block;
    border: 2px solid #b2c0ff;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    margin: 0 22px 0 0;
    transition: transform 0.3s ease, border 0.3s ease;
    &::before,
    &::after {
      content: "";
      position: absolute;
      background: #000;
    }

    &::before {
      top: 50%;
      left: 50%;
      width: 15px;
      height: 4px;
      transform: translate(-50%, -50%);
    }
    &::after {
      top: 50%;
      left: 50%;
      width: 4px;
      height: 15px;
      transform: translate(-50%, -50%);
    }
    &:hover {
      transform: rotate(-90deg);
      border: 2px solid #6976b3;
    }
  }
}
.skills {
  // .skills__items
  margin: 0 0 6% 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 60px 0 0 0;
  }

  // .skills__item

  &__item {
    position: relative;
    width: 49%;
    padding: 15px;
    margin: 0 0 40px 0;
  }
  // skills__num
  &__num {
    font-family: $ffs;
    position: absolute;
    left: 13px;
    top: -40px;
    font-weight: 700;
    font-size: 200px;
    opacity: 0.03;
  }
  // .skills__img

  &__img {
    display: flex;
    align-items: center;
    width: 80px;
    height: 80px;
  }

  // .skills__descr

  &__descr {
    margin: 20px 0 10px 0;
    font-size: 18px;
    font-weight: 700;
  }

  // .skills__text

  &__text {
    font-family: $ffw;
    font-size: 15px;
    line-height: 26px;
  }
}
.reviews {
  position: relative;
  overflow: hidden;
  // reviews__layout
  &__layout {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: -1;
    padding: 0 0 2% 0;
  }

  //reviews__shadow
  &__shadow {
    position: absolute;
    width: 100%;
    img {
      width: 100%;
    }
    &-back {
      position: absolute;
      width: 100%;
      img {
        width: 100%;
      }
    }
  }

  // reviews__bg
  &__bg {
    position: relative;
    width: 100%;
    img {
      width: 100%;
    }
  }
  // reviews__items{
  &__items {
    position: absolute;
    width: 110%;
    img {
      width: 110%;
    }
    top: 21%;
    left: -3% !important;
  }

  //reviews__stars-scene
  &__stars-scene {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
  // reviews__stars
  &__stars {
    width: 100%;
    height: 100%;
    .reviews__star {
      position: absolute;
      &-1 {
        left: 4% !important;
        top: 41% !important;
      }
      &-2 {
        left: 23% !important;
        top: 16% !important;
      }
      &-3 {
        left: 18% !important;
        top: 75.6% !important;
      }
      &-4 {
        left: 20% !important;
        top: 40% !important;
      }
      &-5 {
        right: 15.8% !important;
        left: auto !important;
        top: 19% !important;
      }
      &-6 {
        left: auto !important;
        right: 7.3% !important;
        top: 55% !important;
      }
      &-7 {
        left: 34.5% !important;
        top: 28% !important;
      }
      &-8 {
        left: auto !important;
        right: 33.5% !important;
        top: 14.8% !important;
      }
      &-9 {
        left: 45.2% !important;
        top: 14.5% !important;
        width: 22px;
        height: 22px;
      }
      &-10 {
        left: 28.34% !important;
        top: 28.6% !important;
        width: 22px;
        height: 22px;
      }
      &-11 {
        left: 30.2% !important;
        top: 76% !important;
        width: 22px;
        height: 22px;
      }
      &-12 {
        left: 43% !important;
        top: 50.6% !important;
        width: 22px;
        height: 22px;
      }
    }
  }

  // reviews__content
  &__content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 15px 0 0;
  }

  // .reviews__wrap
  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  // .reviews__inner
  &__inner {
    margin: 40px 80px 0 0;
  }

  // .reviews__descr
  &__descr {
    font-size: 14px;
    text-transform: uppercase;
    margin: 0 0 10px 0;
    color: $wht;
    letter-spacing: 4px;
  }

  // .reviews__title
  &__title {
    font-family: $ffs;
    @include adaptiv-font(50, 20);
    margin: 0 0 40px 0;
    font-weight: 700;
    line-height: 1;
    color: $wht;
  }

  // .reviews__btn

  &__btn {
    color: $wht;
    background-color: transparent;
    border: 2px solid $wht;
    box-shadow: none;
    &:hover {
      background: rgba(0, 222, 252, 0.4);
    }
  }

  // .reviews__slider

  &__slider {
    max-width: 620px;
    overflow: hidden;
    position: relative;
    .swiper-pagination-bullet {
      background-color: #bad0d8;
      opacity: 1;
      &.swiper-pagination-bullet-active {
        background: #4936fa;
      }
    }
  }

  // .reviews__slide

  &__slide {
    max-width: 560px;
    min-height: 245px;
    padding: 30px;
    background-color: #c0f1f3;
  }

  // reviews__pagination
  &__pagination {
    margin: 40px 0 0 15px;
  }

  // .reviews__text

  &__text {
    font-family: $ffp;
    line-height: 28px;
    padding: 20px 0;
  }

  // .reviews__bot

  &__bot {
    display: flex;
    align-items: center;
  }

  // .reviews__img

  &__img {
    position: relative;
    width: 60px;
    height: 60px;
    background-color: #fff;
    border-radius: 50%;
    margin: 0 30px 0 0;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }

  // .reviews__name

  &__name {
    color: #010927;
    font-weight: 300;
  }

  // .reviews__social

  &__social {
    display: flex;
    li {
      font-size: 24px;
      margin: 0 20px 0 0;
      transition: transform 0.3s ease;
      &:hover {
        transform: scale(1.1);
      }
      a {
        color: inherit;
      }
    }
    &-vk {
      color: #2787f5;
    }
    &-fb {
      color: #4267b2;
    }
    &-inst {
      color: #c30777;
    }
  }
}

@media (max-width: 1040px) {
  .top-content {
    &__col {
      &:first-child {
        margin: 0 20px;
        padding: 10px 0 0 0;
      }
      &:last-child {
        margin: 0 20px;
      }
    }
  }
}
@media (max-width: 1024px) {
  .our-services {
    flex-direction: column;
    justify-content: center;
    &__body {
      max-width: 76%;
      margin: 0 auto;
    }
    &__content {
      position: relative;
    }
    &__title {
      margin: 20px 0;
    }
    &__text {
      max-width: 100%;
    }
    &__image {
      margin: 0 0 -10% 0;
    }
  }
  .our-services-left {
    flex-direction: column-reverse;
    .our-services__body {
      margin: 0 auto;
      text-align: left;
      max-width: 76%;
      align-items: flex-start;
    }
    .our-services__text {
      max-width: 100%;
    }
    .our-services__image {
      margin: 0;
    }
  }
  .reviews {
    &__layout {
      display: none;
    }
    &__content {
      padding: 0;
      position: relative;
      transform: none;
    }
    &__wrap {
      flex-direction: column;
    }
    &__inner {
      margin: 20px 0 0 0;
    }
    &__descr {
      color: #3a4366;
    }
    &__title {
      color: #3a4366;
    }
    &__btn {
      color: #3a4366;
      border: 2px solid #3a4366;
    }
    &__pagination {
      text-align: center;
      margin: 40px 0 0;
    }
  }
}
@media (max-width: 1023px) {
  .top-content {
    &__col {
      transition: none;
    }
  }
  .vendors {
    &__wrap {
      margin: 50px 0;
    }
  }
  .steps {
    margin: 20px 0 0 0;
    &__row {
      display: block;
    }
  }
  .portfolio {
    margin: 40px 0 0;
    &__slider {
      margin: 20px 0 0;
    }
  }
  .skills {
    &__num {
      left: 50%;
      transform: translateX(-20%);
    }
    &__img {
      margin: 0 auto;
    }
    &__item {
      text-align: center;
      width: 100%;
    }
  }
}
@media (max-width: 768px) {
  .top-content {
    display: block;
    &__layout {
      position: absolute;
    }
    &__row {
      position: relative;
      display: block;
      margin: 0;
    }
    &__col {
      color: $clr;
      text-align: center;
      &:first-child {
        margin: 0 27%;
      }
      &:last-child {
        margin: 8% 5% 3% 5%;
      }
      h1 {
        color: inherit;
        text-shadow: none;
      }
      p {
        color: inherit;
        max-width: 100%;
        text-shadow: none;
      }
    }
    &__stars {
      .star {
        &-9 {
          display: none;
        }
      }
    }
  }
  .vendors {
    &__wrap {
      margin: 0;
      flex-direction: column;
      a {
        margin: 10px 0;
      }
    }
  }
  .vendors-main {
    .vendors__wrap {
      margin: 20px 0;
    }
  }
  .our-services {
    &__body {
      max-width: 100%;
      padding: 0 15px;
      margin: 0 auto;
    }
    &__text {
      max-width: 100%;
    }
  }
  .our-services-left {
    .our-services__body {
      max-width: 100%;
      padding: 0 15px;
    }
    .our-services__text {
      max-width: 100%;
    }
  }
  .portfolio {
    &__box {
      &.swiper-slide-active {
        .portfolio__img {
          img {
            transform: scale(1.1);
          }
        }
        .portfolio__descr {
          opacity: 1;
          bottom: -89px;
          pointer-events: all;
        }
      }
    }
    &__title {
      margin: 10px 0 0 0;
    }
    &__content {
      display: block;
    }
  }
  .skills {
    &__items {
      margin: 0;
    }
    &__item {
      margin: 0 0 20px 0;
    }
  }
  .reviews {
    &__title {
      margin: 0;
    }
    &__slider {
      width: 100%;
    }
    &__slide {
      max-width: 100%;
      padding: 0;
    }
    &__top {
      padding: 30px 30px 0 30px;
    }
    &__bot {
      padding: 10px 30px 30px 30px;
    }
  }
}
@media (max-width: 480px) {
  .top-content {
    &__stars {
      .star {
        &-1 {
          display: none;
        }
        &-2 {
          display: none;
        }
        &-10 {
          display: none;
        }
        &-14 {
          display: none;
        }
      }
    }
  }
  .description {
    margin: 20px 0 0 0;
  }
  .steps {
    margin: 0;
  }
  .our-services-left {
    .our-services__image {
      margin: 0 0 0 -10%;
    }
  }
  .portfolio {
    &__slider {
      display: block;
    }
    &__list {
      display: inline-block;
      li {
        display: inline-block;
        margin: 10px 20px 10px 0;
      }
    }
  }
}
