.footer {
  // .footer__map
  &__map {
    margin: 100px 0 50px 0;
  }
  // .footer__row
  &__row {
    display: flex;
    justify-content: space-between;
    &:last-child {
      margin: 60px 0 0 0;
      p {
        font-family: $ffw;
      }
    }
  }
  //  footer__logo
  &__logo {
    min-width: 145px;
  }
  // .footer__col
  &__col {
    margin: 0 10px 0 10px;
    &:last-child {
      margin: 0 0 0 10px;
    }
  }
  // .footer__title
  &__title {
    text-transform: uppercase;
    font-family: $ff;
    h4 {
      margin: 0;
    }
  }
  // .footer__list
  &__list {
    font-family: $ffw;
    color: #3a4366;

    li {
      margin: 30px 0;
      a {
        position: relative;
        color: #3a4366;
        font-family: inherit;
        padding: 0 0 5px 0;
        &::before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 2px;
          opacity: 0;
          background-color: $blc;
          transition: opacity 0.3s ease;
        }
        &:hover {
          &::before {
            opacity: 1;
          }
        }
      }
      .footer__job {
        color: #fff;
        border-radius: 5px;
        margin: 0 0 0 10px;
        padding: 5px 10px;
        background-color: #26d9c5;
        text-transform: uppercase;
        transition: background 0.3s ease, color 0.3s ease;
        &:hover {
          background: #fff;
          color: #3a4366;
        }
        &::before {
          content: none;
        }
      }
    }

    &-contact {
      li {
        display: flex;
        align-items: center;
        margin: 20px 0;
        a {
          padding: 5px 0;
          margin: 0 0 0 10px;
        }
      }
    }
  }
  // .footer__markers
  &__markers {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    &::after {
      content: "";
      width: 25px;
      height: 25px;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.5);
      border-radius: 50%;
    }
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    span {
      position: absolute;
      left: 50%;
      top: 44%;
      transform: translate(-50%, -50%);
      color: #4936fa;
    }
  }
  // .footer__policy{
  &__policy {
    color: #3a4366;
    font-size: 15px;
  }
}
//footer__media
@media (max-width: 1023px) {
  .footer {
    &__col {
      display: none;
      &:last-child {
        margin: 0;
      }
    }
    &__contacts {
      display: block;
    }
    &__logo {
      display: block;
      margin: 30px 0;
    }
    &__row {
      display: block;
      &:last-child {
        margin: 30px 0 0 0;
        p {
          line-height: 1.5;
        }
      }
    }
    &__map {
      transition: margin 0.3s ease;
      margin: 30px 0;
    }
  }
}
@media (max-width: 768px) {
  .footer {
    &__logo {
      text-align: center;
    }
    &__row {
      &:last-child {
        text-align: center;
      }
    }
  }
}
@media (max-width: 480px) {
  .footer {
    &__list {
      &-contact {
        li:first-child {
          a {
            &::before {
              content: none;
            }
          }
        }
      }
    }
  }
}
