.pages-head {
  // .pages-head__layout
  position: relative;
  overflow: hidden;
  &__layout {
    height: 100%;
    width: 100%;
    position: absolute;

    // .pages-head__layout_item-1
    &_item {
      position: absolute;
      &-1 {
        left: 0;
        top: 0;
        padding-right: 59%;
      }

      // .pages-head__layout_item-2

      &-2 {
        top: 60%;
        left: 50%;
        transform: translateX(-50%);
      }

      // .pages-head__layout_item-3

      &-3 {
        right: 0;
        padding-left: 75%;
      }

      // .pages-head__layout_item-4

      &-4 {
        right: 0;
        padding-left: 66%;
      }
    }
  }

  // .pages-head__content

  &__content {
    padding: 9.11% 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    text-align: center;
  }

  // .pages-head__title

  &__title {
    font-family: $ffs;
    position: relative;
    color: #fff;
    @include adaptiv-font(60, 24);
    h2 {
      margin: 0;
    }
  }

  // .pages-head__descr

  &__descr {
    font-family: $ffw;
    position: relative;
    color: #fff;
    @include adaptiv-font(20, 16);
  }
}
.info-holder {
  // .info-holder__row

  &__row {
    display: flex;
    align-items: center;
    margin: 125px 0;
  }

  // .info-holder__inner

  &__inner {
    padding: 0 35px 0 0;
  }

  // .info-holder__subtitle

  &__subtitle {
    font-size: 14px;
    text-transform: uppercase;
    color: #899ea7;
  }

  // .info-holder__title

  &__title {
    line-height: 1;
    font-family: $ffs;
    @include adaptiv-font(60, 16);
    margin: 0 0 40px 0;
    h3 {
      margin: 0;
    }
  }

  // .info-holder__descr

  &__descr {
    font-size: 15px;
    font-family: $ffw;
  }

  // .info-holder__img

  &__img {
    position: relative;
    flex: 0 0 50%;
    width: 570px;
    height: 540px;
    border-radius: 5px;
    img {
      border-radius: 5px;
      object-fit: cover;
      position: absolute;
      max-width: 100%;
      width: 100%;
      height: 100%;
      display: block;
    }
  }

  // .info-holder__right

  &__right {
    .info-holder__inner {
      padding: 0 0 0 100px;
      text-align: right;
    }
  }

  // .info-holder__stat

  &__stat {
    flex: 0 0 50%;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
  }

  // .info-holder__item

  &__item {
    position: relative;
    flex: 0 0 50%;
    margin: 20px 0;
    // .info-holder__item-1
    &-1 {
      .info-holder__oval {
        animation: oval 3s linear infinite;
      }
    }
    // .info-holder__item-2
    &-2 {
      .info-holder__oval {
        animation: oval 4s linear infinite;
      }
    }
    // .info-holder__item-3
    &-3 {
      .info-holder__oval {
        animation: oval 2s linear infinite;
      }
    }
    // .info-holder__item-4
    &-4 {
      .info-holder__oval {
        animation: oval 5s linear infinite;
      }
    }
  }

  // .info-holder__oval
  &__oval {
    position: absolute;
    top: 0;
    left: 50%;
  }
  // .info-holder__num
  &__num {
    padding: 17% 0;
    font-family: $ffs;
    font-weight: 700;
    font-size: 30px;
  }
  // .info-holder__text
  &__text {
    margin: 20px 0 0 0;
    font-size: 16px;
    font-weight: 600;
  }
}
.slider-team {
  padding: 100px 0;
  background: #f4fcff;
  // .slider-team__inner

  &__inner {
    text-align: center;
  }

  // .slider-team__subtitle

  &__subtitle {
    font-size: 14px;
    color: #899ea7;
    text-transform: uppercase;
  }

  // .slider-team__title

  &__title {
    margin: 0;
    @include adaptiv-font(60, 20);
    h3 {
      margin: inherit;
    }
  }

  // .slider-team__filters

  &__filters {
    margin: 30px 0 50px 0;
  }

  // .slider-team__list

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      cursor: pointer;
      position: relative;
      margin: 0 20px 0 0;
      transition: color 0.3s ease, background 0.3s ease, height 0.3s ease;
      font-weight: 700;
      color: #000;
      &:last-child {
        margin: 0;
      }
      &._active {
        color: #8e34ef;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          bottom: -5px;
          width: 100%;
          height: 3px;
          opacity: 1;
          background: #8e34ef;
          transition: color 0.3s ease, opacity 0.3s ease;
        }
      }
      a {
        color: inherit;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 3px;
        opacity: 0;
        background: #8e34ef;
        transition: color 0.3s ease, opacity 0.3s ease;
      }

      &:hover {
        color: #8e34ef;
        &::before {
          opacity: 1;
        }
      }
    }
  }

  // .slider-team__img

  &__img {
    width: 270px;
    height: 270px;
    position: relative;
    border-radius: 5px;
    img {
      border-radius: 5px;
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .slider-team__text-top

  &__text-top {
    margin: 30px 0 0 0;
    font-size: 16px;
    font-weight: 600;
  }

  // .slider-team__text-bot

  &__text-bot {
    font-size: 15px;
    line-height: 24px;
  }
}
.general-tabs {
  position: relative;
  padding: 100px 0;

  // .general-tabs__inner

  &__inner {
    border-radius: 10px;
    position: relative;
    padding: 50px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  // .general-tabs__layout
  &__layout {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  //  general-tabs__item
  &__item {
    position: absolute;
    left: -9%;
    top: -75px;
    width: 100%;
    height: 100%;
  }
  //  general-tabs__bg
  &__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  // general-tabs__content
  &__content {
    position: relative;
  }
  // .general-tabs__top

  &__top {
    text-align: center;
  }

  // .general-tabs__subtitle

  &__subtitle {
    color: #fff;
    font-size: 14px;
  }

  // .general-tabs__title

  &__title {
    color: #fff;
    font-family: $ffs;
    font-weight: 700;
    @include adaptiv-font(50, 20);
    margin: 0 0 20px 0;
  }

  // .general-tabs__descr

  &__descr {
    color: #fff;
    font-family: $ffw;
    font-size: 17px;
  }

  // .general-tabs__center

  &__center {
    display: flex;
    justify-content: space-between;
    margin: 80px 0;
    padding: 0 140px;
  }

  // .general-tabs__numbers

  &__numbers {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: $ffs;
    font-weight: 600;
    color: #fff;
    font-size: 36px;
    width: 110px;
    height: 110px;
    border-radius: 50%;
    border: 2px solid #c1eff8;
    cursor: pointer;
    transition: all 0.5s ease;
    &::before {
      content: "";
      position: absolute;
      width: 112%;
      height: 2px;
      background-color: #c1eff8;
      top: 50%;
      left: 100%;
      transform: translateY(-50%);
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
    &._active {
      background-color: #fff;
      color: #31a5ec;
      box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    }
  }

  // .general-tabs__bot

  &__bot {
    position: relative;
    &-steps {
      position: absolute;
      z-index: -1;
      flex: 0 0 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 100px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.4s ease;
      &._active {
        z-index: 1;
        pointer-events: all;
        position: relative;
        opacity: 1;
      }
    }
  }

  // .general-tabs__ps

  &__ps {
    color: #fff;
    font-family: $ffs;
    font-weight: 700;
    @include adaptiv-font(38, 18);
    margin: 0 50px 0 0;
    h4 {
      margin: 0;
    }
  }

  // .general-tabs__text

  &__text {
    color: #fff;
    flex: 0 0 50%;
    font-family: $ffw;
    font-size: 16px;
    line-height: 30px;
  }

  // .general-tabs__button

  &__button {
    position: absolute;
    right: 30px;
    bottom: 30px;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    cursor: pointer;
    transition: all 0.3s ease;
    span {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 12px;
      height: 3px;
      background-color: #36edd9;
      transition: all 0.3s ease;
      &:last-child {
        height: 12px;
        width: 3px;
      }
    }
    &:hover {
      background-color: #26a395;
      span {
        transform: translate(-50%, -50%) rotate(-90deg);
        background-color: #fff;
      }
    }
  }
}
.last-news {
  padding: 100px 0;
  background-color: #f4fcff;
  // .last-news__subtitle

  &__subtitle {
    text-align: center;
    color: #899ea7;
    font-size: 14px;
    text-transform: uppercase;
  }

  // .last-news__title

  &__title {
    text-align: center;
    font-family: $ffs;
    font-weight: 700;
    @include adaptiv-font(60, 24);
    margin: 0 0 90px 0;
  }

  // .last-news__items

  &__items {
    display: flex;
  }

  // .last-news__item

  &__item {
    flex: 0 0 375px;
    margin: 0 20px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  // .last-news__btn

  &__btn {
    display: inline-block;
    padding: 5px 10px;
    border-radius: 4px;
    background-color: #fa8036;
    text-transform: uppercase;
    color: #fff;
    font-weight: 400;
    letter-spacing: 2px;
    box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    margin: 0 0 20px 0;
    transition: all 0.3s ease;
    &:hover {
      background-color: #b45b24;
    }
  }

  // .last-news__descr

  &__descr {
    font-family: $ffs;
    font-size: 20px;
    font-weight: 700;
    margin: 0 0 20px 0;
    min-height: 50px;
  }

  // .last-news__text

  &__text {
    font-size: 15px;
    line-height: 26px;
    color: #3a4366;
    padding: 0 0 20px 0;
  }

  // .last-news__bot

  &__bot {
    display: flex;
    align-items: center;
    border-top: 1px solid #eaeefd;
    padding: 20px 0;
  }

  // .last-news__avatar

  &__avatar {
    position: relative;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #fff;
    margin: 0 20px 0 0;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  // .last-news__name

  &__name {
    font-size: 14px;
    a {
      color: #000;
      font-weight: 700;
      &:hover {
        text-decoration: underline;
      }
    }
  }

  // .last-news__date

  &__date {
    margin: 0 0 0 5px;
  }
}
@keyframes oval {
  0% {
    transform: translateX(-50%) rotate(0deg);
  }

  100% {
    transform: translateX(-50%) rotate(360deg);
  }
}
@keyframes oval2 {
  0% {
    transform: translateX(-50%) rotate(0deg);
  }

  100% {
    transform: translateX(-50%) rotate(360deg);
  }
}
@keyframes oval3 {
  0% {
    transform: translateX(-50%) rotate(0deg);
  }

  100% {
    transform: translateX(-50%) rotate(360deg);
  }
}
@keyframes oval4 {
  0% {
    transform: translateX(-50%) rotate(0deg);
  }

  100% {
    transform: translateX(-50%) rotate(360deg);
  }
}

@media (max-width: 1200px) {
  .info-holder {
    &__item {
      flex: 0 0 245px;
    }
    &__right {
      .info-holder__inner {
        padding: 0 0 0 30px;
      }
    }
  }
  .slider-team {
    &__img {
      width: 100%;
    }
  }
  .general-tabs {
    &__inner {
      padding: 20px;
    }
    &__center {
      margin: 40px 0;
      padding: 0 40px;
    }
    &__numbers::before {
      content: none;
    }
    &__bot-steps {
      padding: 0 40px;
    }
    &__button {
      bottom: 15px;
      right: 15px;
    }
  }
  .last-news {
    &__item {
      flex: 0 0 32%;
    }
  }
}
@media (max-width: 1024px) {
  .info-holder {
    &__row {
      flex-direction: column;
    }
    &__item {
      flex: 0 0 50%;
    }
    &__oval {
      top: 7%;
    }
  }
  .last-news {
    &__items {
      flex-direction: column;
      text-align: center;
    }
    &__bot {
      justify-content: center;
    }
  }
}
@media (max-width: 768px) {
  .info-holder {
    &__row {
      margin: 30px 0;
    }
    &__inner {
      padding: 0;
    }
    &__item {
      flex: 0 0 100%;
    }
    &__stat {
      margin: 0 0 50px 0;
    }
    &__oval {
      top: 0;
    }
    &__num {
      padding: 50px 0;
    }
    &__title {
      margin: 0 0 20px 0;
    }
    &__right {
      .info-holder__inner {
        padding: 0;
      }
    }
  }
  .slider-team {
    padding: 50px 0;
    &__filters {
      margin: 20px 0;
    }
    &__list {
      flex-direction: column;
      li {
        margin: 10px 0;
        &:last-child {
          margin: 10px 0;
        }
      }
    }
  }
  .general-tabs {
    padding: 50px 0;
    &__center {
      flex-direction: column;
      align-items: center;
    }
    &__numbers {
      margin: 20px 0;
    }
    &__bot-steps {
      padding: 0 20px;
      flex-direction: column;
    }
    &__ps {
      margin: 0;
      text-align: center;
    }
    &__text {
      text-align: center;
    }
  }
  .last-news {
    padding: 50px 0;
    &__item {
      padding: 15px;
      border: 1px solid #eaeefd;
      margin: 15px 0;
      box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.2);
    }
    &__title {
      margin: 20px 0 50px 0;
    }
    &__descr {
      min-height: auto;
    }
  }
}
@media (max-width: 480px) {
}
